type Contact = {
  API: string;
};

const deleteOnClick = (id: string, setList: any): void => {
  const localItems: Contact[] = JSON.parse(
    localStorage.getItem("contact") || "[]"
  );
  const filtered: Contact[] = localItems.filter(
    (contact) => contact.API !== id
  );
  setList(filtered);
  localStorage.setItem("contact", JSON.stringify(filtered));
};
export default deleteOnClick;
