import React, { FC, useEffect, useState, ChangeEvent, Suspense } from "react";
import { Link, Outlet } from "react-router-dom";
import { SearchComponent } from "./Search/SearchComponent";
import { Loader } from "../Loader/Loader";
import { fetchBase, fetchData } from "../dataFetch/dataFetch";
import { getLocaleItem } from "./TS/getLocalItem";
import deleteOnClick from "./TS/deleteItem";
import { randomOnline } from "./TS/randomOnline";
import "./sidebar.css";

interface APIEntry {
  API: string;
  Description?: string;
}

const Sidebar: FC = () => {
  const localItems = JSON.parse(localStorage.getItem("contact") || "[]");
  const [list, setList] = useState<APIEntry[]>([]);
  const fetchBaseAw = async () => {
    const data: APIEntry[] = await fetchBase();
    if (localItems.length > 0) {
      setList(localItems);
    } else {
      setList(data);
    }
  };
  useEffect(() => {
    fetchData();
    fetchBaseAw();
  }, []);
  const handleOnChange = (e: ChangeEvent<HTMLInputElement>) => {
    const search = e.currentTarget.value.toLowerCase();
    const localList = getLocaleItem();
    const filtered: APIEntry[] = localList.filter(({ API }) =>
      API.toLowerCase().includes(search)
    );
    setList(filtered);
  };
  const handleDeleteClick = (e: React.MouseEvent<HTMLButtonElement>): void => {
    const contactId: string = e.currentTarget.id;
    deleteOnClick(contactId, setList);
  };
  return (
    <div className="backdrop">
      <div className="main">
        <header className="header">Messenger</header>
        <div className="container">
          <div className="sidebar">
            <SearchComponent handleOnChange={handleOnChange} />
            {list.map(({ API }) => (
              <Link to={`${API}`} className="contact" key={API}>
                <span className="name">{API}</span>
                <span className="status" style={{ color: randomOnline() }}>
                  Online
                </span>
                <button
                  className="btn_item"
                  id={API}
                  onClick={handleDeleteClick}
                >
                  X
                </button>
              </Link>
            ))}
          </div>
          <Suspense fallback={<Loader />}>
            <Outlet />
          </Suspense>
        </div>
      </div>
    </div>
  );
};

export default Sidebar;
